import { Component, Input, EventEmitter, Output, AfterViewInit, forwardRef, OnInit, SimpleChanges, ChangeDetectorRef, ChangeDetectionStrategy } from '@angular/core';
import { BcCardSelectV2Behavior, BcCardProductV2Config, BcFooterButtonV2Config,BcCardSelectV2Style } from '@bancolombia/design-system-behaviors';
import { BcGeneralInputComponent, BcHelperService } from '@bancolombia/design-system-web/bc-helpers';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { Subject } from 'rxjs';
import { SEARCH_PLACEHOLDER } from './bc-card-select-v2.constants';


/**
 * Componente Card Select
 *
 * @export
 * @class BcCardSelectV2Component
 * @implements {OnInit}
 * @implements {AfterViewInit}
 */
@Component({
  selector: 'bc-card-select-v2',
  templateUrl: 'bc-card-select-v2.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => BcCardSelectV2Component),
      multi: true,
    },
  ],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BcCardSelectV2Component extends BcGeneralInputComponent implements OnInit, AfterViewInit, AfterViewInit {
  /**
   * Esta entrada permite indicar un id único al componente dentro del DOM.
   *
   * @type {string}
   * @memberof BcCardSelectV2Component
   */
  @Input() componentId: string = this.bcHelperService.getId(this);

  /**
   * Esta entrada permite indicar el titulo para el card select.
   *
   * @type {string}
   * @memberof BcCardSelectV2Component
   */
  @Input() titleCardSelect: string;

  /**
   * Esta entrada permite indicar el subtitulo para el card select.
   *
   * @type {string}
   * @memberof BcCardSelectV2Component
   */
  @Input() subTitleCardSelect: string;

  /**
  * Configuración del botón del dropdown, acá se define el icono y el texto.
  *
  * @type {BcFooterButtonConfig}
  * @memberof BcCardSelectV2Component
  */
  @Input() configDropDownButton: BcFooterButtonV2Config;

  /**
   * Muestra el button del dropdown por defecto en falso
   * true | false
   *
   * @type {boolean}
   * @memberof BcCardSelectV2Component
   */
  @Input() showDropDowButton: boolean = true;

  /**
   * Esta entrada es el arreglo de configuración de las card del componente
   *
   * @type {Array<BcCardProductConfig>}
   * @memberof BcCardSelectV2Component
   */
  @Input() values: Array<BcCardProductV2Config>;

  /**
   * Esta entrada configura el valor por defecto.
   *
   * @type {string | number}
   * @memberof BcCardSelectV2Component
   */
  @Input() defaultValue: string | number;

  /**
  * Esta entrada habilita o deshabilita el componente.
  *
  * @type {boolean}
  * @memberof BcCardSelectV2Component
  */
  @Input() disabled: boolean = false;

  /**
  * Texto en caso de que no existan datos en el select
  *
  * @type {string}
  * @memberof BcCardSelectV2Component
  */
  @Input() messageNotData: string ='';
  
  /**
  * Texto en caso de que no existan datos en el select
  *
  * @type {string}
  * @memberof BcCardSelectV2Component
  */
  @Input() componentStyle: BcCardSelectV2Style;
     /**
   * Esta entrada permite reiniciar el componente
   *
   * @type {boolean}
   * @memberof BcCardSelectV2Component
   */
     @Input() resetComponent: boolean ;

   /**
   * Esta entrada permite indicar el placeholder para el input search del card select.
   *
   * @type {string}
   * @memberof BcCardSelectV2Component
   */
    @Input() searchPlaceholder: string = SEARCH_PLACEHOLDER;
    /**
   * Permite ocultar la flecha 
   *
   * @type {boolean}
   * @memberof BcCardSelectV2Component
   */
    @Input() hideArrow: boolean =false;
  /**
   * Evento que retorna el valor del item seleccionado.
   *
   * @type {(EventEmitter<any>)}
   * @memberof BcCardSelectV2Component
   */
  @Output() valueOutput: EventEmitter<any> = new EventEmitter<any>();
  /**
   * Evento que se emite cuando se da clic al button action
   *
   * @type {(EventEmitter<any>)}
   * @memberof BcCardSelectV2Component
   */
  @Output() actionButtonOutput: EventEmitter<any> = new EventEmitter<any>();

   /**
   * Cambios en el input del search del componente.
   *
   * @type {(EventEmitter<any>)}
   * @memberof BcCardSelectV2Component
   */
  @Output() changeValueOutput: EventEmitter<any> = new EventEmitter<any>();
  /**
   * variable que hace referencia al comportamiento nativo del componente
   * @memberof BcCardSelectV2Component
   */
  bcCardSelectBehavior;
  /**
   * Valor actual
   * @memberof BcCardSelectV2Component
   */
  currentValue;
  /**
   * Configuración inicial del componente
   * @memberof BcCardSelectV2Component
   */
  config = {
    firstLoad: true,
  };
  /**
   * Configuración de los cambios del componente
   * @memberof BcCardSelectV2Component
   */
  onChanges = new Subject<SimpleChanges>();
  


  constructor(
    private bcHelperService: BcHelperService,
    private changeDetectorRef: ChangeDetectorRef,
    ) {
      super();
      this.bcCardSelectBehavior = new BcCardSelectV2Behavior();

    }
  ngOnInit(): void {
   this.setEvents();
  }

  async ngAfterViewInit() {
    this.initComponent();
    this.setChanges();
  }

   /**
   * Registro y envío de los cambios del componente
   * @param {SimpleChanges} changes
   * @memberof BcCardSelectV2Component
   */
  ngOnChanges(changes: SimpleChanges): void {
   this.onChanges.next(changes);
  }
  /**
   * Destrucción del componente
   * @memberof BcCardSelectV2Component
   */
  ngOnDestroy(): void {
    this.onChanges.complete();
  }

  /**
   * Inicialización y configuración del componente
   * @memberof BcCardSelectV2Component
   */
  initComponent(): void {
    this.bcCardSelectBehavior.setId(this.componentId);  
    if (this.configDropDownButton) {
      this.bcCardSelectBehavior.dropdownButtonConfig = {
        event: () => this.actionButtonOutput.emit(),
        icon: this.configDropDownButton.icon,
        text: this.configDropDownButton.text,
      }
    }
    this.bcCardSelectBehavior.setItems(this.values);
    this.bcCardSelectBehavior.setMessageNotData(this.messageNotData);
    if(this.componentStyle){this.bcCardSelectBehavior.setComponentStyle(this.componentStyle);}
    this.bcCardSelectBehavior.setUp();
    this.bcCardSelectBehavior.setHideArrowState(this.hideArrow);
    this.bcCardSelectBehavior.setIniText(this.titleCardSelect);
    this.bcCardSelectBehavior.setSubtitleText(this.subTitleCardSelect);
    this.bcCardSelectBehavior.hideDropdown(!this.showDropDowButton);
    this.bcCardSelectBehavior.setDefaultValue(this.defaultValue);
    this.bcCardSelectBehavior.setDisable(this.disabled);

  }
  /**
   *Captura de eventos desde el behavior y el emit de los mismos en el componente.
   * @memberof BcCardSelectV2Component
   */
  setEvents(){
    this.bcCardSelectBehavior.eventValueOutput=(item:any)=> {
      this.valueOutput.emit(item);
    };
    this.bcCardSelectBehavior.returnValueInput=(key:any)=> {
      this.changeValueOutput.emit(key);
    };
   
  }
  
  /**
   *Actualización del estado del componente con respecto a los parámetros.
   * @memberof BcCardSelectV2Component
   */
  setChanges(){
    this.onChanges.subscribe((changes:SimpleChanges)=>{
      if (changes.showDropDowButton ) {
          this.bcCardSelectBehavior.hideDropdown(!this.showDropDowButton);
      }
      if (changes.defaultValue ) {
        this.bcCardSelectBehavior.setDefaultValue(this.defaultValue);
      }
      if (changes.disabled ) {
        this.bcCardSelectBehavior.setDisable(this.disabled);
      }
      if (changes.resetComponent ) {
        this.bcCardSelectBehavior.resetComponent();
      }
      if (changes.hideArrow ) {
        this.bcCardSelectBehavior.setHideArrowState(this.hideArrow);
    }
    });
  }
}